<template>
  <div class="course-content ql-editor">
    <div class="course-detail">
      <div class="head-title">
        <p>{{ courseInfo.title }}</p>
        <img
          :src="imgSrc + '20200217181050881/1581934250593.png'"
          alt="标题"
          width="120"
          height="34"
        />
      </div>
      <div v-if="courseInfo.content" class="key-contents">
        <img :src="imgSrc + '20200217181124860/1581934284493.png'" alt="重点" />
        <div class="info">
          <div class="item" v-html="courseInfo.content" />
        </div>
      </div>
      <el-divider />
      <m-video v-if="courseInfo.type === 'video'" :video-src="courseInfo.url" />
      <audio
        v-if="courseInfo.type === 'audio'"
        class="audio-player"
        :src="courseInfo.url"
        controls
      >
        您的浏览器不支持 audio 标签。
      </audio>
      <div class="course-detail-content">
        <div class="detail" v-html="courseInfo.detail" />
        <div class="center">
          <el-button type="primary" @click="goTest">去测试</el-button>
        </div>
      </div>
    </div>
    <div
      v-if="courseInfo.category !== 'single_lesson' && courseList.length"
      class="right-content"
    >
      <div class="head-info">
        <img
          :src="imgSrc + '20200217181205808/1581934325478.png'"
          alt="logo"
          class="logo"
        />
        <p>认真学习 丨 快速成长</p>
        <p>公司使命：让天下孕妇儿童更加健康快乐</p>
        <p>公司愿景：做用户喜欢和依赖的的领先科技企业</p>
        <p>公司价值观：用户第一，自我驱动，拥抱变化</p>
      </div>
      <el-divider />
      <div class="list">
        <div v-for="(item, index) of courseList" :key="index" class="item">
          <p class="title-info">
            <img
              v-if="item.type === 'video'"
              :src="imgSrc + '20200217180816740/1581934096405.png'"
              alt="视频"
            />
            <img
              v-if="item.type === 'audio'"
              :src="imgSrc + '20200217180902469/1581934142175.png'"
              alt="音频"
            />
            <img
              v-if="item.type === 'image_text' || !item.type"
              :src="imgSrc + '20200217180936086/1581934175789.png'"
              alt="图文"
            />
            <router-link :to="`/study/course-detail/${item._id}`"
              ><span :class="item._id === $route.params.id ? 'active' : ''"
                >{{ index + 1 }}.{{ item.title }}</span
              ></router-link
            >
          </p>
          <p class="time">
            {{ item.created_at.split('T')[0] }}
            {{ item.created_at.split('T')[1].split('.')[0] }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { detailCourse, getCourseList } from '@/api/learningAdmin'
import MVideo from './components/video'
import 'vue2-editor/dist/vue2-editor.css'
export default {
  components: {
    MVideo
  },
  props: {
    courseDetail: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      courseInfo: '',
      courseList: [],
      imgSrc:
        'https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//'
    }
  },
  watch: {
    courseDetail() {
      this.courseInfo = this.courseDetail
      this.detailCourse()
    }
  },
  created() {
    this.$store.state.study.start_study_time = new Date()
  },
  mounted() {
    if (!this.courseInfo) this.detailCourse()
  },
  methods: {
    async detailCourse() {
      const id = this.$route.params.id || this.courseDetail._id
      const { data, code } = await detailCourse(id)
      if (code === 20000 && data.result) {
        if (data.mtsCatalog.lesson_column !== 'catalog')
          this.getCourseDetailList(data.mtsCatalog.parent_id)
        this.courseInfo = data.mtsCatalog
      }
    },
    goTest() {
      const id = this.$route.params.id || this.courseDetail._id
      this.$router.push('/study/test/' + id + '?title=' + this.courseInfo.title)
    },
    async getCourseDetailList(id) {
      if (this.courseInfo.total_chapter === 0) return
      const { data, code } = await getCourseList({
        id
      })
      if (code === 20000 && data.result) {
        this.mtsCatalog = data.mtsCatalog
        const isSingle = data.list.every(item => {
          return item.type && item.type.length < 11
        })
        if (isSingle) this.courseList = data.list
      }
    }
  }
}
</script>

<style scoped lang="scss">
.course-content {
  display: flex;
  min-height: 100%;
  .course-detail {
    padding: 40px 20px;
    width: 100%;
    min-height: 100%;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    .head-title {
      margin: 0 auto 50px;
      padding: 15px 30px;
      width: max-content;
      height: 34px;
      border: 1px solid #409eff;
      border-radius: 5px;
      p {
        margin-top: -24px;
        padding: 0 5px;
        text-align: center;
        background-color: #fff;
      }
      img {
        margin-top: 10px;
        margin-left: 50%;
        transform: translate(-50%);
      }
    }
    .key-contents {
      display: flex;
      margin-top: 20px;
      min-height: 80px;
      img {
        width: 110px;
        height: 54px;
      }
      .info {
        margin-left: 20px;
        padding-top: 10px;
        .item {
          font-size: 14px;
          color: #999;
          margin-bottom: 10px;
          /deep/p,
          /deep/h1,
          /deep/h2,
          /deep/h3,
          /deep/h4,
          /deep/h5,
          /deep/h6 {
            margin: 5px 0;
            &:first-child {
              margin-top: 0;
            }
          }
        }
      }
    }
    .course-detail-content {
      padding: 0 120px;
      .detail {
        line-height: 20px;
        font-size: 16px;
        color: #576277;
        /deep/p {
          margin: 5px 0;
        }
        /deep/img {
          max-width: 100% !important;
        }
      }
    }
    .center {
      margin-top: 50px;
      text-align: center;
    }
    .el-button {
      padding: 6px 20px;
      height: 32px;
    }
    .el-input {
      height: 32px;
      input {
        height: 32px;
      }
    }
  }
  .right-content {
    margin-left: 20px;
    padding: 20px;
    width: 492px;
    border: 1px solid #ebeef5;
    .head-info {
      text-align: center;
      .logo {
        display: inline-block;
        width: 50px;
        height: 50px;
      }
      p {
        margin: 10px 0;
        font-size: 14px;
        color: #17233d;
      }
    }
    .list {
      .item {
        p {
          margin: 10px 0 14px;
        }
        p.title-info {
          img {
            margin-right: 10px;
            width: 12px;
            height: 12px;
          }
          span {
            font-size: 14px;
            color: #576277;
            &.active {
              color: #2d8cf0;
            }
          }
        }
        p.time {
          font-size: 12px;
          color: #999;
        }
      }
    }
  }
  .audio-player {
    display: block;
    margin: 0 auto 30px;
    width: 80%;
    height: 30px;
  }
}
</style>
